@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-shadow: 2px 2px 6px black;
}

body {
    font-family: 'Big Shoulders Text', cursive;
    line-height: 1.4;
    background-color: rgb(204, 173, 108);
    color: rgb(255, 255, 255);
    /* white-space: nowrap; */
}

.header {
    text-align: left;
    background: #333;
}

.navbar {
    background: #333;
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.33);
}

.navButton {
    background:none;
    border:none;
    color:rgb(136, 136, 136);
}

.navButton:hover {
    color: white;
}

.modal-content {
    background-color: #333;
    text-align: center;
    word-wrap: break-word;
    white-space: normal;
    white-space: initial;
}

.infocolOverride {
    padding: 0px;
    margin-left: 15px;
    margin-right: 15px;
}


.inputbox {
    display: flex;
}

.mock-input {
    color: rgb(255, 148, 49);
}

.info-input {
    text-shadow: none;
    background: #333;
    color: rgb(255, 222, 115);
    border: none;
    flex: 1 1;
    padding-left: 4px;
}

input {
    text-shadow: none;
    background: #333;
    border: none;
}   

.mock-input-dropdown {
    color: rgb(255, 222, 115);
    border: none;
    flex: 1 1;
}
/* we want text shadow off of every input except characterinfo */
.characterinfo-box input {
    text-shadow: 2px 2px 6px black;
}

.characterinfo-box{
    /* background: rgb(153, 153, 153); */
    background: #333;
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.33);
}


.characterinfo-content{
    /* background: rgb(153, 153, 153); */
    background: #333;
    margin: 15px;
}

.character-list-slot {
    text-shadow: none;
    outline-style: auto;
    outline-color: black;
    background-color: rgb(139, 0, 0);
}

.dropdown-menu {
    background-color: #333;
    padding: 0;
}

a {
    color: #333;
    text-decoration: none;
}

.container {
    padding: 0 1rem;
}

.collapser-header {
    text-align: center;
    background: #333;
    /* box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.33); */
    position: relative;
}

.character-bio {
    text-align: center;
    background: #333;
    box-shadow: 2px 4px 4px 1px rgba(0, 0, 0, 0.33);
    justify-content: center;
    position: relative;
}

.character-bio-text {
    min-width: 300px;
    min-height:200px;
    text-align: center;
    background: #333;
    color: white;
    resize: none;
    border: none;
    outline: none;
    overflow-x: hidden;
}

.character-bio-input {
    color: white;
    min-width: 300px;
}

.character-bio-input::-webkit-input-placeholder {
    text-align: center;
}

.character-bio-input:-ms-input-placeholder {
    text-align: center;
}

.character-bio-input::-ms-input-placeholder {
    text-align: center;
}

.character-bio-input::placeholder {
    text-align: center;
}

.character-picture {
    width: 250px;
    height: 250px;
    text-align: center;
    justify-content: center;
    border: 2px solid #6a6a6a;
    object-fit: scale-down;
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.33);
}

.resourcebox {
    text-align: center;
    background: #333;
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.33);
    justify-content: center;
    position: relative;
}

.resource-cost {
    position:absolute; 
    right: 7%;
    margin-top: 6px;
}

.rscrow {
    padding-bottom: 5px;
    margin-left: 0px;
    margin-right: 0px;
}

.rscbtn {
    padding:5px;
    box-shadow: 1px 1px 4px black;
}

.btn-dark {
    border-color: #6a6a6a;
}

.progress-bar {
    position: relative;
    height: 23px;
    width: 90%;
    border-radius: 5px;
    margin-top: 5px;
    margin-left: 2px;
    margin-right: 2px;
    border: 1px solid #333;
    background-color: #304b68;
}

.bar-text {
    position:absolute;      
    display:block;
    height:100%;
    width: 100%;
    padding-bottom: 22px;
    text-align:center;
    box-shadow: 2px 2px 4px black;
}
  
.filler {
    background: #1DA598;
    height: 100%;
    border-radius: inherit;
}

.filler.magic{
    background: rgb(20, 102, 255);
}

.filler.production {
    background: rgb(34, 165, 29);
}

.filler.craft{
    background: rgb(202, 155, 0);
}

.skill {
    background-color: rgb(61, 0, 0);
    box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.33);
    border-radius: 3px;
    margin: 2px;
}

.skillBody {
    color: rgb(161, 161, 161);
}

.checked {
    color: rgb(255, 255, 255);
    background-color: rgb(139, 0, 0);
}

.skillCost {
    color:white;
}
.skilltree {
    min-width: 273px;
}

.skilltree h3 {
    text-align: center;
    background: #333;
    box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.33);
    margin-bottom: 0;
    margin-top: 10px;
}

.skilltree p {
    white-space: pre;
    margin:2px;
}

.skilltree .combat h3 {
    color: rgb(226, 43, 43);
}

.skilltree .production h3 {
    color: rgb(4, 156, 67);
}

.skilltree .roleplaying h3 {
    color: rgb(243, 202, 66);
}

.skilltree .general h3 {
    color: rgb(226, 95, 43);
}

.skilltree .aegis h3 {
    color: rgb(150, 186, 202);
}

.skilltree .compulsion h3 {
    color: rgb(226, 43, 150);
}

.skilltree .nature h3 {
    color: rgb(132, 202, 3);
}

.skilltree .battle h3 {
    color: rgb(248, 46, 46);
}

.skilltree .restoration h3 {
    color: rgb(34, 240, 255);
}

.skilltree .necromancy h3 {
    color: rgb(171, 117, 179);
}

.skilltree .resources h3 {
    color: rgb(243, 223, 135);
}

.skilltree .enchantment h3 {
    color: rgb(124, 133, 255);
}

.skillpointsremaining ::-webkit-input-placeholder {
    color: white;
    opacity: 1;
}

.skillpointsremaining :-ms-input-placeholder {
    color: white;
    opacity: 1;
}

.skillpointsremaining ::-ms-input-placeholder {
    color: white;
    opacity: 1;
}

.skillpointsremaining ::placeholder {
    color: white;
    opacity: 1;
}

.alert {
    white-space: normal;
    white-space: initial;
    text-shadow: none;
}

.info-dropdown-item{
    color: white;
}

.imgbox {
    position: relative;
    margin: auto;
    overflow: hidden;
  }
  
  .imgbox .imgbox-overlay {
    background: rgba(0,0,0,0.7);
    position: absolute;
    height: 99%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    -webkit-transition: all 0.4s ease-in-out 0s;
    transition: all 0.4s ease-in-out 0s;
  }
  
  .imgbox:hover .imgbox-overlay{
    opacity: 0.3;
  }
  
  .imgboxtext {
      /* display: flex;
      flex-wrap: wrap; */
    position: absolute;
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
    width: 100%;
    top: 50%;
    left: 50%;
    opacity: 0;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }

  .imgbox:hover .imgboxtext{
    top: 90%;
    left: 50%;
    opacity: 1;
  }
